require('./cause-and-effect.css');

window.chart = (function () {

    // Hack to prevent iframe from screen capture
    var item = sessionStorage.getItem('capturingScreen');
    if(item || !parent.jQuery) return;
    
    // Pull jQuery from parent
    if (typeof (jQuery) == "undefined") {
        var iframeBody = document.getElementsByTagName("body")[0];
        var jQuery = function (selector) { return parent.jQuery(selector, iframeBody); };
        var $ = jQuery;
    }

    // Define empty matrix & find all required DOM elements 
    var cell = null;
    var minRow = 20;
    var matrix = { options: null, causes: [], effects: [], selectedCause: null, selectedEffect: null }
    var headers = $('#causeHeader table thead')[0];
    var causes = $('#causes table tbody')[0];
    var effects = $('#effectTable tbody')[0];
    var actions = $('#actions table tbody')[0];
    var causeContainer = $('#causes');
    var actionContainer = $('#actions');
    var effectContainer = $('#effectTable tbody');
    var headerContainer = $('#causeHeader');

    // Resize
    function resize(factor){
        headerContainer.css('width', window.innerWidth - (697*factor));
        causeContainer.css('height', window.innerHeight - (430*factor));
        effectContainer.css('height', window.innerWidth - (698*factor));
        actionContainer.css('height', window.innerHeight - (413*factor));
        actionContainer.css('width', window.innerWidth - (679*factor));
    }

    // Init chart
    function init(options, cell, errors) {
        resize(window.innerWidth < 1400 ? 0.81 : 1);
        matrix.options = options;
        initCauses(options.rows);
        initEffects(options.columns);
        initCauseHeader(options.columns);
        initActions(options.rows, options.columns);
        loadMatrix(cell, errors);
    }
    function initCauses(rows, offset) {
        for (var r = 0; r < rows; r++) {
            var tr = document.createElement('tr');
            if(offset)
                tr.setAttribute('data-row', r + offset);
            else
                tr.setAttribute('data-row', r);
            for (var i = 0; i < 7; i++) {
                if (i === 0)
                    tr.appendChild(createCell('<div class="empty-cell"></div>', null));
                else if (i === 6){
                    if(offset)
                        tr.appendChild(createCell(r + 1 + offset, null));
                    else
                        tr.appendChild(createCell(r + 1, null));
                } else
                    tr.appendChild(createCell(null, null));
            }
            causes.appendChild(tr);
        }
    }
    function initEffects(columns) {
        for (var c = 0; c < columns; c++) {
            var tr = document.createElement('tr');
            appendEffect(tr);
            effects.appendChild(tr);
        }
    }
    function appendEffect(tr){
        for (var i = 0; i < 2; i++) {
            if (i === 0)
                tr.appendChild(createCell('<div class="empty-cell"></div>', null));
            else
                tr.appendChild(createCell(null, null));
        }
    }
    function initCauseHeader(columns) {
        for (var c = 0; c < columns; c++)
            appendHeader(c + 1);
    }
    function initActions(rows, columns, offset) {
        for (var r = 0; r < rows; r++)
            appendActionRow(r, columns, offset);
    }
    function createCell(content, attrs) {
        var td = document.createElement('td');
        if (content)
            td.innerHTML = content;
        if (attrs) {
            for (var attr in attrs)
                td.setAttribute(attr, attrs[attr]);
        }
        return td;
    }
    function appendActionRow(row, columns, offset) {
        var tr = document.createElement('tr');
        if(offset)
            tr.setAttribute('data-row', row + offset);
        else
            tr.setAttribute('data-row', row);
        for (var c = 0; c < columns; c++) {
            var td = createCell('<div class="empty-cell"></div>', { 'data-column': c });
            td.onmouseover = onMouseover;
            td.onmouseout = onMouseout;
            tr.appendChild(td);
        }
        actions.appendChild(tr);
    }
    function appendActionColumn(column) {
        for (var i = 0; i < actions.children.length; i++) {
            var td = createCell('<div class="empty-cell"></div>', { 'data-column': column });
            td.onmouseover = onMouseover;
            td.onmouseout = onMouseout;
            actions.children[i].appendChild(td);
        }
    }
    function appendHeader(number) {
        var th = document.createElement('th');
        th.innerHTML = number;
        headers.children[0].appendChild(th);
    }
    function loadMatrix(cell, errors){
        if(!cell.cAE_Matrix) return;
        var data = JSON.parse(cell.cAE_Matrix);
        var removedCauses = 0;
        for(var i=0; i<errors.causes.length; i++){
            data.causes.splice(errors.causes[i] - removedCauses, 1);
            data.actions.splice(errors.causes[i] - removedCauses, 1);
            removedCauses++;
        }
        var removedEffects = 0;
        for(var i=0; i<errors.effects.length; i++){
            data.effects.splice(errors.effects[i] - removedEffects, 1);
            for(var k=0; k<data.actions.length; k++)
                data.actions[k].splice(errors.effects[i] - removedEffects, 1);
            removedEffects++;
        }
        for(var i=0; i<data.causes.length; i++)
            addCause(data.causes[i]);
        for(var i=0; i<data.effects.length; i++)
            addEffect(data.effects[i]);
        for(var i=0; i<actions.children.length; i++){
            var row = actions.children[i];
            for(var k=0; k<row.children.length; k++){
                var cell = row.children[k].children[0];
                if(cell.nodeName === 'DIV') continue;
                cell.checked = data.actions[i][k]; 
            }
        }
    }

    // Synchronise scrolling, vertically & horizontally
    var scrollTop = 0;
    actionContainer.on('scroll', function (event) {
        if (scrollTop !== $(this).scrollTop()) {
            causeContainer.scrollTop($(this).scrollTop());
            scrollTop = $(this).scrollTop();
        } else {
            headerContainer.scrollLeft($(this).scrollLeft());
            effectContainer.scrollTop($(this).scrollLeft());
        }
    });

    // Define cell highlighting
    function onMouseover(event) {
        var target = getTarget(event);
        for (var c = 0; c < target.parentElement.children.length; c++)
            target.parentElement.children[c].style.backgroundColor = matrix.options.highlightColour;
        var row = parseInt(target.parentElement.getAttribute('data-row'));
        causes.children[row].style.backgroundColor = matrix.options.highlightColour;
        var column = parseInt(target.getAttribute('data-column'));
        for (var r = 0; r < actions.children.length; r++)
            actions.children[r].children[column].style.backgroundColor = matrix.options.highlightColour;
        effects.children[column].style.backgroundColor = matrix.options.highlightColour;
        if(target.children[0].nodeName === 'INPUT')
            target.title = getTitle(matrix.causes[row], matrix.effects[column]);
    }
    function onMouseout(event) {
        var target = getTarget(event);
        for (var c = 0; c < target.parentElement.children.length; c++)
            target.parentElement.children[c].style.backgroundColor = '';
        var row = parseInt(target.parentElement.getAttribute('data-row'));
        if(matrix.selectedCause === row)
            causes.children[row].style.backgroundColor = '#03EBFF';
        else
            causes.children[row].style.backgroundColor = '';
        var column = parseInt(target.getAttribute('data-column'));
        for (var r = 0; r < actions.children.length; r++)
            actions.children[r].children[column].style.backgroundColor = '';
        if(matrix.selectedEffect === column)
            effects.children[column].style.backgroundColor = '#D903FF';
        else
            effects.children[column].style.backgroundColor = '';
        target.title = '';
    }
    function onClick(event) {
        if (event.target.nodeName !== 'TD') return;
        if (!event.target.children.length || event.target.children[0].nodeName !== 'INPUT') return;
        event.target.children[0].checked = !event.target.children[0].checked;
    }

    // Define row selection handlers
    function onCauseClick(event){
        if(matrix.selectedCause !== null){
            causes.children[matrix.selectedCause].style.backgroundColor = '';
            causes.children[matrix.selectedCause].style.fontWeight = 100;
        }
        var rowIndex = parseInt(event.target.parentElement.getAttribute('data-row'));
        if(matrix.selectedCause === rowIndex)
            matrix.selectedCause = null;
        else {
            event.target.parentElement.style.backgroundColor = '#03EBFF';
            event.target.parentElement.style.fontWeight = 'bold';
            matrix.selectedCause = rowIndex;
        }
    }
    function onEffectClick(event){
        if(matrix.selectedEffect !== null) {
            effects.children[matrix.selectedEffect].style.backgroundColor = '';
            effects.children[matrix.selectedEffect].style.fontWeight = 100;
        }
        var rowIndex = parseInt(event.target.parentElement.getAttribute('data-row'));
        if(matrix.selectedEffect === rowIndex)
            matrix.selectedEffect = null;
        else {
            event.target.parentElement.style.backgroundColor = '#D903FF';
            event.target.parentElement.style.fontWeight = 'bold';
            matrix.selectedEffect = rowIndex;
        }
    }

    // Find target cell
    function getTarget(event) {
        if (event.target.nodeName === 'INPUT' || event.target.nodeName === 'DIV')
            return event.target.parentElement;
        else
            return event.target;
    }

    // Enumerations
    var func = Object.freeze({ Null: '', AND: 'AND', OR: 'OR' });

    // Add cause
    function addCause(tempCause) {
        var cause = {
            description: tempCause.description,
            func: tempCause.func,
            inputs: tempCause.inputs.slice()
        }
        var tr = document.createElement('tr');
        tr.onclick = onCauseClick;
        tr.style.cursor = 'pointer';
        var nodeIds = cause.inputs.map(function(row){ return row.nodeId; }).join('\n');
        var operators = cause.inputs.map(function(row){ return row.operator; }).join('\n');
        var values = cause.inputs.map(function(row){ return row.value; }).join('\n');
        var units = cause.inputs.map(function(row){ return row.units; }).join('\n');
        tr.appendChild(createCell(nodeIds));
        tr.appendChild(createCell(func[cause.func]));
        tr.appendChild(createCell(operators));
        tr.appendChild(createCell(values));
        tr.appendChild(createCell(units));
        tr.appendChild(createCell(cause.description));
        tr.appendChild(createCell(matrix.causes.length + 1));
        if (causes.children.length !== matrix.causes.length)
            causes.replaceChild(tr, causes.children[matrix.causes.length]);
        else {
            causes.appendChild(tr);
            appendActionRow(matrix.causes.length, effects.children.length);
        }
        cause.row =  matrix.causes.length;
        tr.setAttribute('data-row', cause.row);
        matrix.causes.push(cause);
        updateEffectInputs(cause.row);
        updateRowHeight(cause);
    }

    // Edit cause
    function editCause(cause){
        var matrixCause = causes.children[cause.row];
        var nodeIds = cause.inputs.map(function(row){ return row.nodeId; }).join('\n');
        var operators = cause.inputs.map(function(row){ return row.operator; }).join('\n');
        var values = cause.inputs.map(function(row){ return row.value; }).join('\n');
        var units = cause.inputs.map(function(row){ return row.units; }).join('\n');
        matrixCause.children[0].innerHTML = nodeIds;
        matrixCause.children[1].innerHTML = cause.func;
        matrixCause.children[2].innerHTML = operators;
        matrixCause.children[3].innerHTML = values;
        matrixCause.children[4].innerHTML = units;
        matrixCause.children[5].innerHTML = cause.description;
        for(var i=0; i<matrix.causes.length; i++){
            if(matrix.causes[i].row === cause.row){
                matrix.causes[i] = {
                    description: cause.description,
                    func: cause.func,
                    inputs: cause.inputs.slice(),
                    row: cause.row
                }
            }
        }
        updateRowHeight(cause);
    }

    // Remove cause
    function removeCause(cause){
        causes.removeChild(causes.children[cause.row]);
        actions.removeChild(actions.children[cause.row]);
        matrix.causes.splice(cause.row, 1);
        for(var i=0; i<causes.children.length; i++){
            causes.children[i].children[6].innerHTML = i + 1;
            causes.children[i].setAttribute('data-row', i);
            actions.children[i].setAttribute('data-row', i);
            if(matrix.causes[i])
                matrix.causes[i].row = i;
        }
        matrix.selectedCause = null;
        if(minRow - causes.children.length > 0){
            var numOfRowsToAppend = minRow - causes.children.length;
            var offset = causes.children.length;
            initCauses(numOfRowsToAppend, offset);
            initActions(numOfRowsToAppend, effects.children.length, offset);
        }
    }

    // Add effect
    function addEffect(effect) {
        var tr = document.createElement('tr');
        tr.onclick = onEffectClick;
        tr.style.cursor = 'pointer';
        tr.appendChild(createCell(effect.opcRequest.objectId + '.' + effect.action));
        tr.appendChild(createCell(effect.description));
        if (effects.children.length !== matrix.effects.length)
            effects.replaceChild(tr, effects.children[matrix.effects.length]);
        else {
            effects.appendChild(tr);
            appendHeader(matrix.effects.length + 1);
            appendActionColumn(matrix.effects.length);
        }
        if(effect.opcRequest.arguments.length)
            tr.children[0].title = effect.opcRequest.arguments[0].value;
        effect.row = matrix.effects.length;
        tr.setAttribute('data-row', effect.row);
        matrix.effects.push(effect);
        updateCauseInputs(effect.row);
    }

    // Edit effect
    function editEffect(effect){
        var matrixEffect = effects.children[effect.row];
        matrixEffect.children[1].innerHTML = effect.description;
        for(var i=0; i<matrix.effects.length; i++){
            if(matrix.effects[i].row === effect.row)
                matrix.effects[i] = effect;
        }
        if(effect.opcRequest.arguments.length)
            effects.children[effect.row].children[0].title = effect.opcRequest.arguments[0].value;
    }

    // Remove effect
    function removeEffect(effect){
        effects.removeChild(effects.children[effect.row]);
        if(effects.children.length < 40){
            var tr = document.createElement('tr');
            appendEffect(tr);
            effects.appendChild(tr);
        }
        matrix.effects.splice(effect.row, 1);
        for(var i=0; i<actions.children.length; i++){
            actions.children[i].removeChild(actions.children[i].children[effect.row]);
            if(actions.children[i].children.length > 40) continue;
            var td = createCell('<div class="empty-cell"></div>', { 'data-column': 39 });
            td.onmouseover = onMouseover;
            td.onmouseout = onMouseout;
            actions.children[i].appendChild(td)
        }
        for(var i=0; i<effects.children.length; i++){
            effects.children[i].setAttribute('data-row', i);
            if(matrix.effects[i])
                matrix.effects[i].row = i;
        }
        for(var i=0; i<actions.children.length; i++){
            for(var c=0; c<actions.children[i].children.length; c++)
                actions.children[i].children[c].setAttribute('data-column', c);
        }
        matrix.selectedEffect = null;
    }

    // Update action heights
    function updateRowHeight(cause){
        if(cause.inputs.length === 1)
            actions.children[cause.row].style.height = '29px';
        else if(cause.inputs.length === 2)
            actions.children[cause.row].style.height = '35px';
        else if(cause.inputs.length === 3)
            actions.children[cause.row].style.height = '51px';
    }

    // Get cell title
    function getTitle(cause, effect) {
        return cause.description + ' => ' + effect.description;
    }

    // Update effects along cause row
    function updateEffectInputs(row) {
        var actionRow = actions.children[row];
        for (var i = 0; i < matrix.effects.length; i++) {
            if (actionRow.children[i].children[0].nodeName !== 'DIV') return;
            var td = createCell('<input type="checkbox"/>', { 'data-column': i });
            td.onmouseover = onMouseover;
            td.onmouseout = onMouseout;
            td.onclick = onClick;
            actionRow.replaceChild(td, actionRow.children[i]);
        }
    }

    // Update causes along effect row
    function updateCauseInputs(row) {
        for (var i = 0; i < matrix.causes.length; i++) {
            if (actions.children[i].children[row].children[0].nodeName !== 'DIV') return;
            var td = createCell('<input type="checkbox"/>', { 'data-column': row });
            td.onmouseover = onMouseover;
            td.onmouseout = onMouseout;
            td.onclick = onClick;
            actions.children[i].replaceChild(td, actions.children[i].children[row]);
        }
    }

    // Export matrix
    function exportMatrix() {
        var acts = [[]];
        for(var i=0; i<actions.children.length; i++){
            var row = actions.children[i];
            for(var k=0; k<row.children.length; k++){
                var cell = row.children[k].children[0];
                if(!acts[i])
                    acts[i] = [];
                if(cell.nodeName === 'DIV')
                    acts[i][k] = false;
                else    
                    acts[i][k] = cell.checked;
            }
        }
        return JSON.stringify({
            causes: matrix.causes,
            effects: matrix.effects,
            actions: acts
        });
    }

    // Clear matrix
    function clear(){
        while(causes.firstChild)
            causes.removeChild(causes.firstChild);
        while(effects.firstChild)
            effects.removeChild(effects.firstChild);
        while(actions.firstChild)
            actions.removeChild(actions.firstChild);
        var headerCount = headers.children[0].children.length; 
        while(headerCount-- && headerCount > 6)
            headers.children[0].removeChild(headers.children[0].children[headerCount]);
        matrix.causes = [];
        matrix.effects = [];
        matrix.selectedCause = null;
        matrix.selectedEffect = null;
        cell = null;
    }

    // Public API
    return {
        cell: cell,
        resize: resize,
        init: init,
        addCause: addCause,
        editCause: editCause,
        removeCause: removeCause,
        addEffect: addEffect,
        editEffect: editEffect,
        removeEffect: removeEffect,
        matrix: matrix,
        exportMatrix: exportMatrix,
        clear: clear
    }

})();






